.sidebar {
    position: fixed;
    z-index: 3;
    left: -100%;
    min-height: 100%;
    width: 317px;
    padding: 70px;
    background-color: #fff;
}

.sidebar.open {
    left: 0;
}

.background {
    position: absolute;
    z-index: 2;
    background-color: rgba(0, 0, 0, 0.45);
    min-width: 100%;
    min-height: 100%;
}

.nav {
    display: flex;
    flex-direction: column;
    gap: 2.2em;
}

.nav a {
    font-weight: 600;
    font-size: 1.375rem;
    text-decoration: none;
    color: inherit;
}

.nav a:hover {
    color: #58ACFF;
}

.nav a:active {
    color: #3D6B99;
}

.nav a.active {
    text-decoration: underline;
    color: #0059B2;
}

.close {
    position: absolute;
    top: 0;
    right: 0;
    background-color: transparent;
    border: none;
}

.close img {
    padding: 10px;
}
